// ES6
import Vue from 'vue'
import GetTextPlugin from 'vue-gettext'
import translations from '../translations/translations'

Vue.use(GetTextPlugin,
    {
        defaultLanguage: 'vi',
        translations: translations,
        availableLanguages: {
            en_US: 'English',
            vi: 'Vietnameses',
        },
    })


Vue.filter('translate', value => {
  return !value
    ? ''
    : Vue.prototype.$gettext(value.toString())
})