import Vue from 'vue'
import CloudjetNav from '../templates/components/demo_app/main'
import CloudjetNav2 from '../templates/components/demo_app/main'
import ExscoreMainModal from '../templates/components/kpi_editor/exscore_lib/main'


/*
* Only expose application component to library, minor components won't expose here. It's the main component "main.vue"
* For examples:
* - kpi-row
* - weight-edit-modal
* - import-kpis-modal
*
* */

const Components = {
    CloudjetNav, // explicit declare components library hereq
    CloudjetNav2,
    ExscoreMainModal,
}


/* Automatic register component */
Object.keys(Components).forEach(name => {
    Vue.component(name,Components[name])
})

export default Components